.feedback {
  font-family: TTLakes, sans-serif;
  z-index: 3;
  position: absolute;
  right: 5px;
  width: 320px;
  background-color: white; }

#feedback {
  opacity: 1; }

#feedback.hidden {
  display: none; }

#feedback.low-opacity {
  opacity: 0.1; }

.Feedback-item {
  padding: 16px; }

.navigation-close_feedback {
  margin-left: 14px; }

.Feedback-head {
  margin-left: 14px; }

.Feedback-name {
  width: 95%;
  height: 48px;
  flex-shrink: 0;
  margin-left: 14px; }

.Feedback-number {
  width: 95%;
  height: 48px;
  flex-shrink: 0;
  margin-left: 14px; }

.Feedback-email {
  width: 95%;
  height: 48px;
  flex-shrink: 0;
  margin-left: 14px; }

.Feedback-text {
  width: 95%;
  height: 118px;
  flex-shrink: 0;
  margin-left: 14px; }

.Feedback-confirm {
  display: -ms-grid;
  display: grid;
  justify-items: end;
  margin-left: 14px; }

.Feedback-confirm_button {
  padding: 16px; }

.feedback-animation-entry {
  transform: translateX(-150%);
  -webkit-animation: anim 1s forwards;
  animation: anim 1s forwards; }

@-webkit-keyframes anim {
  0% {
    transform: translateX(150%); }
  100% {
    transform: translateY(0%); } }

@keyframes anim {
  0% {
    transform: translateX(150%); }
  100% {
    transform: translateY(0%); } }

@media (min-width: 768px) {
  .feedback {
    width: 440px;
    position: absolute;
    right: 1px;
    top: 10px;
    z-index: 2;
    background-color: white; }
  .Feedback-item {
    padding: 16px; }
  .Feedback-name {
    width: 288px;
    height: 48px;
    flex-shrink: 0; }
  .Feedback-number {
    width: 288px;
    height: 48px;
    flex-shrink: 0; }
  .Feedback-email {
    width: 288px;
    height: 48px;
    flex-shrink: 0; }
  .Feedback-text {
    width: 288px;
    height: 118px;
    flex-shrink: 0; }
  .Feedback-confirm {
    display: -ms-grid;
    display: grid;
    justify-items: end; } }
