.navigation {
  background-color: #fff;
  border-top: 1px #d9fff5 solid;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center; }

.navigation * {
  border: 2px transparent solid;
  border-radius: 22px; }

.navigation * :hover {
  border: 2px #41f6d7 solid; }

.navigation-border2 {
  display: none; }

#navigation {
  opacity: 1; }

#navigation.hidden {
  display: none; }

#navigation.low-opacity {
  opacity: 0.1; }

.navigation-burger {
  order: 1; }

.navigation-border1 {
  order: 3; }

.navigation-logo {
  order: 2; }

.navigation-settings {
  order: 4; }

.navigation-search {
  order: 5; }

.settings-span_desktop {
  display: none; }

#mobile-hidden {
  display: none; }

@media (min-width: 768px) {
  #mobile-hidden {
    display: block; }
  .navigation-border2 {
    display: block;
    order: 7; }
  .navigation-burger {
    order: 1; }
  .navigation-border1 {
    order: 3; }
  .navigation-logo {
    order: 2; }
  .navigation-settings {
    order: 8; }
  .navigation-search {
    order: 9; }
  .navigation-phone {
    order: 4; }
  .navigation-chat {
    order: 5; }
  .navigation-profile {
    order: 6; } }

@media (min-width: 1440px) {
  .navigation {
    display: none; } }
